<template>
    <div class="navbar">
        <div class="navbar-wrapper">
            <div class="navbar-left">
                <a href="#" class="navbar-logo">
                    <img :src="logo" alt="logo" />
                </a>

                <div class="navbar-resize" @click="smallNav">
                    <i class="icofont-outdent"></i>
                </div>
                <div class="navbar-resize full-screen" @click="requestFullScreen">
                    <i class="icofont-expand"></i>
                </div>
                <div class="navbar-search">
                    <input type="text" placeholder="Поиск..." />
                    <span class="icofont icofont-search"></span>
                </div>
            </div>
            <div class="navbar-right">
                <div class="lang nav-lang" @click="isLangOpen = !isLangOpen">
                        <img :src="languages[langIndex].img" :alt="languages[langIndex].name" class="nav-lang__flag" />
                        <p>
                            {{ languages[langIndex].name }}
                        </p>
                        <img class="nav-lang__chevron" :src="chevron" alt="">
                        <ul v-if="isLangOpen" class="nav-lang__list">
                            <li v-for="(lang, index) in languages" :key="lang.name"
                                @click.stop="changeHandler(index, lang.value)">
                                <img :src="lang.img" :alt="lang.name" class="nav-lang__flag" />{{ lang.name }}
                            </li>
                        </ul>
                    </div>
                <div class="navbar-profile">
                    <img :src="profile" alt="profile" />
                    <div class="navbar-profile__menu" :class="{ 'active-profile': activeProfile }">
                        <ul>
                            <li>
                                <a href="#">Мой профиль</a>
                            </li>
                            <li>
                                <a href="#">Календарь</a>
                            </li>
                            <li><a href="#" @click.prevent="logout">Выход</a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <teleport href="#">
            <div class="overlay-mobile" @click="$emit('removeElement')"></div>
        </teleport>
    </div>
</template>
  
<script>
export default {
    data() {
        return {
            interval: null,
            activeNotif: false,
            activeProfile: false,
            windowWidth: 0,
            profile: require('@/assets/img/profile/profile.png'),
            logo: require('@/assets/img/logo.svg'),
            languages: [
                {
                    name: "RU",
                    img: "https://media.istockphoto.com/vectors/russian-flag-flat-layout-vector-illustration-vector-id691333844?k=20&m=691333844&s=612x612&w=0&h=ldRcKgg8kVwX7eChE1si5PRVOU6oRna5HrF2hlms7Yo=",
                },
                { name: "УЗ", img: "https://www.crwflags.com/fotw/images/u/uz.gif" },
                { name: "O'Z", img: "https://www.crwflags.com/fotw/images/u/uz.gif" },
                {
                    name: "EN",
                    img: "https://upload.wikimedia.org/wikipedia/commons/thumb/d/d8/Flag_of_the_USA.png/800px-Flag_of_the_USA.png",
                },
            ],
            langIndex: 0,
            isLangOpen: false,
        };
    },
    methods: {
        smallNav() {
            document.body.classList.toggle("small-sidenav");
            if (this.windowWidth < 725) {
                document.body.classList.remove("small-sidenav");
                document.body.classList.toggle("mobile-sidenav");
            }

            this.$emit("makeFalse");
        },
        requestFullScreen() {
            const body = document.body;
            const requestMethod =
                body.requestFullScreen ||
                body.webkitRequestFullScreen ||
                body.mozRequestFullScreen ||
                body.msRequestFullScreen;

            if (requestMethod) {
                // Native full screen.
                requestMethod.call(body);
            } else if (typeof window.ActiveXObject !== "undefined") {
                // Older IE.
                const wscript = new ActiveXObject("WScript.Shell");

                if (wscript !== null) {
                    wscript.SendKeys("{F11}");
                }
            }
        },
        getWindowWidth(event) {
            this.windowWidth = document.documentElement.clientWidth;
        },
        changeHandler(idx) {
            this.langIndex = idx;
            this.isLangOpen = false;
        },
    },
    mounted() {
        this.$store.dispatch("renderDate");
        document.body.addEventListener("click", (e) => {
            e.target.closest(".notification") &&
                !e.target.closest(".notification .all")
                ? (this.activeNotif = true)
                : (this.activeNotif = false);

            e.target.closest(".navbar-profile") &&
                !e.target.closest(".navbar-profile__menu > ul > li > a")
                ? (this.activeProfile = true)
                : (this.activeProfile = false);

            e.target.closest(".lang")
                ? (this.isLangOpen = true)
                : (this.isLangOpen = false);
        });

        this.$nextTick(function () {
            window.addEventListener("resize", this.getWindowWidth);

            //Init
            this.getWindowWidth();
        });
    },
    beforeDestroy() {
        this.$store.commit("CLEAR_INTERVAL");
        window.removeEventListener("resize", this.getWindowWidth);
    },
};
</script>
  
<style></style>
  