<template>
    <div>
      <div v-for="(item, index) in accordionItems" :key="index">
        <div class="accordion-header" :class="{ active: item.isActive }" @click="toggleAccordion(index)">
          <span class="accordion-title">{{ item.title }}</span>
          <span class="accordion-icon" :class="{ 'accordion-icon-open': item.isActive }"></span>
        </div>
        <transition name="accordion">
          <div v-show="item.isActive" class="accordion-content">
            {{ item.content }}
          </div>
        </transition>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        accordionItems: [
          {
            title: 'Ваше приложение безопасно?',
            content: 'Content for Accordion Item 1',
            isActive: false
          },
          {
            title: 'Что такое МИС система и как она может помочь моей медицинской организации?',
            content: 'Content for Accordion Item 2',
            isActive: false
          },
          {
            title: 'Как безопасна система Avimed для хранения и обработки медицинских данных?',
            content: 'Content for Accordion Item 3',
            isActive: false
          },
          {
            title: 'Какая поддержка доступна для клиентов, использующих систему Avimed?',
            content: 'Content for Accordion Item 4',
            isActive: false
          },
          {
            title: 'Может ли Avimed интегрироваться с другими медицинскими системами и устройствами?',
            content: 'Content for Accordion Item 5',
            isActive: false
          }
        ]
      };
    },
    methods: {
      toggleAccordion(index) {
        this.accordionItems.forEach((item, i) => {
          if (i === index) {
            item.isActive = !item.isActive;
          } else {
            item.isActive = false;
          }
        });
      }
    }
  };
  </script>
  
  <style>

  </style>
  