<template>
    <section class="verification">
        <div class="container">
            <div class="verification-wrapper">
                <div class="verification-left">
                    <div class="verification-left__brand reg-brand">
                        <router-link to="/" class="btn verification-back reg-back"><i
                                class="far fa-home-alt"></i></router-link>
                        <a href="#!" class="verification-logo reg-logo"><img :src="logo" alt=""></a>
                    </div>
                    <h3 class="verification-left__title reg__title">Verification</h3>
                    <p class="verification-left__txt reg__txt">Мы отправили вам SMS с кодом на номер +9989 97 808 44 66 для
                        использования системы avimed. </p>
                    <form action="" class="verification-form">
                        <div class="verification-inputs">
                            <input v-for="(digit, index) in verificationDigits" :key="index"
                                v-model.number="verificationDigits[index]" type="text" name="verification-digit"
                                :id="'verification-digit-' + index" maxlength="1" class="verification-form__input"
                                :inputmode="getInputMode()" @keydown="handleKeyDown">
                        </div>
                        <a href="#" class="verification-wrong">SMS not recieved?</a>
                        <router-link class="reg-btn" to="/registration">Дальше </router-link>
                    </form>


                </div>
                <div class="verification-right">
                    <img :src="top" alt="" class="right-top right-absolute">
                    <img :src="bottom" alt="" class="right-bottom right-absolute">

                    <img :src="logoWhite" alt="" class="logo-white">
                    <h3 class="verification-right__slogan right-slogan">ELEVATING HEALTHARE EXELLENCE</h3>

                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    data: () => ({
        verificationDigits: ['', '', '', '', ''],
        logo: require('@/assets/img/logo.svg'),
        logoWhite: require('@/assets/img/registration/logo.svg'),
        top: require('@/assets/img/registration/top.png'),
        bottom: require('@/assets/img/registration/bottom.png'),
    }),
    methods: {
        getInputMode() {
            return 'numeric';
        },
        handleKeyDown(event) {
            const keyCode = event.keyCode;
            // Разрешить только цифры
            if (!(keyCode >= 48 && keyCode <= 57) && !(keyCode >= 96 && keyCode <= 105)) {
                event.preventDefault();
            }
        }
    }
}
</script>

<style></style>