<template>
    <section class="phone">
        <div class="container">
            <div class="phone-wrapper">
                <div class="phone-left">
                    <div class="phone-left__brand reg-brand">
                        <router-link to="/" class="btn phone-back reg-back"><i class="far fa-home-alt"></i></router-link>
                        <a href="#!" class="phone-logo reg-logo"><img :src="logo" alt=""></a>
                    </div>
                    <h3 class="phone-left__title reg__title">Введите номер телефона</h3>
                    <p class="phone-left__txt reg__txt">Вы введете язык системы на следующих шагах, выбрав язык</p>

                    <form action="http://localhost:8080/#/Verification" class="phone-form">
                        <label for="phone" class="phone-form__label">Номер телефона</label>
                        <input v-model="phone" type="tel" name="phone" id="phone" placeholder="+998 -- --- -- --"
                            @input="addPrefix" class="phone-form__input" required>
                        <router-link to="/verification" class="reg-btn">Дальше </router-link>
                    </form>
                </div>
                <div class="phone-right">
                    <img :src="top" alt="" class="right-top right-absolute">
                    <img :src="bottom" alt="" class="right-bottom right-absolute">

                    <img :src="logoWhite" alt="" class="logo-white">
                    <h3 class="phone-right__slogan right-slogan">ELEVATING HEALTHARE EXELLENCE</h3>

                </div>
            </div>
        </div>
    </section>
</template>

<script>

export default {

    data: () => ({
        logo: require('@/assets/img/logo.svg'),
        logoWhite: require('@/assets/img/registration/logo.svg'),
        top: require('@/assets/img/registration/top.png'),
        bottom: require('@/assets/img/registration/bottom.png'),

        phone: ''
    }),

    methods: {
        addPrefix() {
            this.phone = this.phone.replace(/[^0-9+]/g, '');

            if (!this.phone.startsWith('+998')) {
                this.phone = '+998' + this.phone;
            }
        }
    }
}
</script>

<style></style>